import PropTypes from "prop-types";
import CustomPagination from "@components/customPaginationV1";
import CustomSelectDropdown from "@components/customSelectDropdown";
import { Home, CheckCircle } from "react-feather";
import { FormattedMessage, injectIntl } from "react-intl";
import { Button, notification, Table } from "antd";
import { ReactComponent as FilterIcon } from "@src/assets/images/icons/filter.svg";
import { ReactComponent as SearchIcon } from "@src/assets/images/icons/search.svg";
import Sidebar from "@components/sidebar";
import PickedFilter from "@components/filters/filterComponents/PickedFilter";
import { countNotEmptyProperties } from "@src/helper/object";

import "antd/dist/antd.css";
import "./PickedResDatatable.scss";
import { DropdownItem } from "reactstrap";

const PickedResDatatable = ({
    loading,
    tableData,
    selectedRows,
    setSelectedRows,
    totalRecords,
    totalPages,
    currentPage,
    setCurrentPage,
    visibleColumns,
    setVisibleColumns,
    sidebarVisibility,
    setSidebarVisibility,
    selectedFilterValues,
    setSelectedFilterValues,
    intl
}) => {
    // rowSelection object indicates the need for row selection
    const rowSelection = {
        onChange: (_, sltRows) => setSelectedRows(sltRows),
    };

    const handleColumnsToggle = (selectedColumn) => {
        const newColumns = visibleColumns.map((column) => {
            if (column.dataIndex === selectedColumn.dataIndex) {
                return { ...column, visible: !column.visible };
            }
            return column;
        });
        setVisibleColumns(newColumns);
    };

    function multiAction() {
        selectedRows.forEach((row) => {
            notification.info({
                message: `id ${Object.values(row.id)} selected`,
            });
        });
    }

    function toggleSidebar() {
        setSidebarVisibility(!sidebarVisibility);
    }

    return (
        <div className="rp-border-radius-24 rp-background-white">
            <div className="rp-border-radius-24-top rp-background-purple rp-table-header d-flex flex-column">
                <div className="d-flex align-items-baseline justify-content-between">
                    <div className="d-flex align-items-baseline">
                        <Home size={20} color="#fff" />
                        <span className="ml-2 rp-table-header-title rp-color-main">
                            <FormattedMessage id="pickedRes" />
                        </span>
                        <span className="ml-1 rp-table-header-title-sub rp-color-main">
                            <FormattedMessage
                                id="XTotal"
                                values={{ x: totalRecords }}
                            />{" "}
                            {}
                        </span>
                    </div>
                    <div className="d-flex align-items-center">
                        <div className="rp-input-wrapper">
                            <SearchIcon style={{fill: "white"}} />{" "}
                            <input
                                className="input-box"
                                placeholder={intl.formatMessage({id: "enterKeyword"})}
                                value={selectedFilterValues?.searchText}
                                onChange={({ target: { value } }) =>
                                    setSelectedFilterValues({
                                        ...selectedFilterValues,
                                        searchText: value,
                                    })
                                }
                            />
                        </div>

                        <div className="rp-button-wrapper">
                            <Button
                                className="button-box"
                                onClick={toggleSidebar}
                            >
                                <FilterIcon className="mr-1" />{" "}
                                <FormattedMessage id="filter" />(
                                {countNotEmptyProperties(selectedFilterValues)})
                            </Button>
                        </div>
                    </div>
                </div>
            </div>
            <div className="d-flex flex-column align-items-center">
                <Table
                    id="pickedResTable"
                    className="w-100"
                    columns={visibleColumns.filter((item) => item.visible)}
                    dataSource={tableData}
                    rowKey={(record) =>
                        Object.values(Object.values(record)[0])[0]
                    }
                    // rowSelection={rowSelection}
                    pagination={false}
                    sticky
                    scroll={{ x: true }}
                    loading={loading}
                />
                <CustomPagination
                    totalPages={totalPages}
                    currentPage={currentPage}
                    setCurrentPage={setCurrentPage}
                />
            </div>
            <Sidebar
                open={sidebarVisibility}
                toggleSidebar={toggleSidebar}
                closeBtn={false}
                width="500"
                title=""
            >
                <PickedFilter
                    values={selectedFilterValues}
                    setValues={setSelectedFilterValues}
                />
            </Sidebar>
        </div>
    );
};

export default injectIntl(PickedResDatatable);

PickedResDatatable.propTypes = {
    tableHeader: PropTypes.arrayOf(PropTypes.object),
    tableData: PropTypes.arrayOf(PropTypes.object),
    selectedRows: PropTypes.arrayOf(PropTypes.object),
    setSelectedRows: PropTypes.func,
    totalRecords: PropTypes.number,
    recordsPerPage: PropTypes.number,
    totalPages: PropTypes.number,
    currentPage: PropTypes.number,
    setCurrentPage: PropTypes.func,
};

PickedResDatatable.defaultProps = {
    tableData: [],
    selectedRows: [],
    setSelectedRows: () => {},
    totalRecords: 0,
    recordsPerPage: 2,
    totalPages: 1,
    currentPage: 0,
    setCurrentPage: () => {},
    visibleColumns: [],
    setVisibleColumns: () => {},
    sidebarVisibility: false,
    setSidebarVisibility: () => {},
    selectedFilterValues: {},
    setSelectedFilterValues: () => {},
    fetchData: () => {},
};
