import { MoreVertical } from "react-feather";
import { ReactComponent as Share } from "@src/assets/images/icons/share.svg";
import {
    UncontrolledDropdown,
    DropdownItem,
    DropdownMenu,
    DropdownToggle,
} from "reactstrap";
import { Link } from 'react-router-dom';
import { FormattedMessage } from "react-intl";

import DesiredCellLayout from "@components/cell-grid/CustomCellLayout";

const COLUMN_WIDTH = {
    ID: "9%",
    ADDRESS: "30%",
    LAND: "10%",
    HOUSE: "10%",
    JURIDICAL: "10%",
    MEDIA: "15%",
    SCORE: "15%",
    ACTION: "1%",
}

export const COLUMN_CONFIG = [
    {
        dataIndex: "id",
        title: (
            <div>
                <FormattedMessage id="id" />
            </div>
        ),
        fixed: true,
        className: "",
        render: (id) => <Link to={`/view/${Object.values(id).toString().padStart(1, 0)}`} replace>#{Object.values(id).toString().padStart(6, 0)}</Link>,
        onCell: (_) => ({
            style: {
                color: "blue",
                textDecoration: "underline",
            },
        }),
        width: COLUMN_WIDTH.ID,
        visible: true,
    },
    {
        dataIndex: "address",
        title: (
            <div className="pl-1 text-uppercase">
                <FormattedMessage id="address" />
            </div>
        ),
        fixed: false,
        className: "pl-0",
        render: (address) => DesiredCellLayout({item: address, layout: {cols: 1, isMultipleRow: false, reverseData: false}}),
        onCell: (_) => ({
            className: "pl-1",
        }),
        width: COLUMN_WIDTH.ADDRESS,
        visible: true,
    },
    {
        dataIndex: "land",
        title: (
            <div className="text-uppercase">
                <FormattedMessage id="land" />
            </div>
        ),
        fixed: false,
        className: "pl-0",
      render: (land) => DesiredCellLayout({ item: land, layout: { cols: 2, isMultipleRow: true, reverseData: false } }),
        onCell: (_) => ({}),
        width: COLUMN_WIDTH.LAND,
        visible: true,
    },
    {
        dataIndex: "house",
        title: (
            <div className="text-uppercase">
                <FormattedMessage id="house" />
            </div>
        ),
        fixed: false,
        className: "pl-0",
        render: (house) => DesiredCellLayout({item: house, layout: {cols: 2, isMultipleRow: true, reverseData: false}}),
        onCell: (_) => ({}),
        width: COLUMN_WIDTH.HOUSE,
        visible: true,
    },
    {
        dataIndex: "juridical",
        title: (
            <div className="text-uppercase">
                <FormattedMessage id="juridical" />
            </div>
        ),
        fixed: false,
        className: "pl-0",
        render: (juridical) => DesiredCellLayout({item: juridical, layout: {cols: 1, isMultipleRow: true, reverseData: false}}),
        onCell: (_) => ({}),
        width: COLUMN_WIDTH.JURIDICAL,
        visible: true,
    },
    {
        dataIndex: "media",
        title: (
            <div className="text-uppercase">
                <FormattedMessage id="media" />
            </div>
        ),
        fixed: false,
        className: "pl-0",
        render: (media) => DesiredCellLayout({item: media, layout: {cols: 1, isMultipleRow: true, reverseData: true}}),
        onCell: (_) => ({}),
        width: COLUMN_WIDTH.MEDIA,
        visible: true,
    },
    {
        dataIndex: "score",
        title: (
            <div className="text-uppercase">
                <FormattedMessage id="score" />
            </div>
        ),
        fixed: false,
        className: "pl-0",
        render: (score) => DesiredCellLayout({item: score, layout: {cols: 1, isMultipleRow: true, reverseData: false}}),
        onCell: (_) => ({}),
        width: COLUMN_WIDTH.SCORE,
        visible: true,
    },
    {
        dataIndex: "action",
        title: "",
        fixed: false,
        className: "p-0",
        render: (_, record) => (
            <div className="d-flex">
                <UncontrolledDropdown>
                    <DropdownToggle className="pr-1" tag="span">
                        <MoreVertical size={15} />
                    </DropdownToggle>
                    <DropdownMenu right>
                        <DropdownItems record={record} />
                    </DropdownMenu>
                </UncontrolledDropdown>
            </div>
        ),
        onCell: (_) => ({}),
        width: COLUMN_WIDTH.ACTION,
        visible: true,
    },
];

export const COLUMN_DATA_CONFIG = {
    columns: [
        "id",
        "address",
        "land",
        "house",
        "juridical",
        "media",
        "score",
        "status",
    ],
    columnGroup: [
        ["realEstateRawId"],
        [
            "realEstateLocationHomeNumber",
            "areaStreetName",
            "areaWardName",
            "areaDistrictName",
            "areaProvinceName",
            "areaCountryName",
        ],
        [
            "realEstateLandWidth",
            "realEstateLandLongs",
            "realEstateLandRoadSquare",
            "realEstateLandRealConstructionSquare",
            "realEstateLandRealitySquare",
        ],
        [
            "realEstateHouseFloors",
            "realEstateHouseBedRooms",
            "realEstateHouseToilets",
            "realEstateHouseFurnitureList",
        ],
        [
            "realEstateJuridical",
            "realEstateJuridicalName",
            "realEstateValueSalePrice",
            "realEstateUnitPrice",
        ],
        [
            "realEstateHouseImage",
            "realEstateVideo",
            "realEstateBluePrintImage"
        ],
        [
            "realEstateCollectionPoint",
            "realEstateInvestmentPoint",
            "realEstateRatePoint",
            "realEstateLastPoint",
        ],
        [
            "approveStatus",
            "approveDate",
            "approvePIC"
        ],
    ],
};

const DropdownItems = ({ record }) => (
    <>
        <DropdownItem
            tag="a"
            href="/"
            className="w-100"
            onClick={(e) => {
                e.preventDefault();
                navigator.clipboard.writeText(
                    window.location.href + "/" + Object.values(record.id)
                );
            }}
        >
            <Share size={15} />
            <span className="align-middle ml-50">
                <FormattedMessage id="shareRes" />
            </span>
        </DropdownItem>
    </>
);
