import { Fragment, useEffect, useState } from "react";
import PickedResDatatable from "./PickedResDatatableV2";
import { COLUMN_CONFIG, COLUMN_DATA_CONFIG } from "./sampleData";
import responseHelper from "../../../helper/response";
import RealEstateServices from "../../../services/post";
import { REQUEST_CONFIG } from "../../../constants/request";
import { ENDPOINTS } from "../../../constants/endpoints";

const RawResTable = () => {
    // State for table
    const [selectedRows, setSelectedRows] = useState([]);
    const [totalRecords, setTotalRecords] = useState(0);
    const [totalPages, setTotalPages] = useState(0);
    const [currentPage, setCurrentPage] = useState(0);
    const [visibleColumns, setVisibleColumns] = useState(COLUMN_CONFIG);
    const [tableData, setTableData] = useState([]);
    const [loading, setLoading] = useState(false);

    // State for filter
    // State for selected values from filter
    const [selectedFilterValues, setSelectedFilterValues] = useState({});

    // State for sidebar
    const [sidebarVisibility, setSidebarVisibility] = useState(false);

    function fetchData() {
        setTableData([]);
        setLoading(true);
        RealEstateServices.getList(
            {
                [REQUEST_CONFIG.method]: "POST",
                [REQUEST_CONFIG.path]: ENDPOINTS.RES_PICKED_LIST,
            },
            {
                ...selectedFilterValues,
                order: {
                    key: "approveDate",
                    value: "desc",
                }
            },
            currentPage
        ).then(({ data, totalRecord: total, totalPage: pages }) => {
            const result = responseHelper.groupProperties(
                data,
                COLUMN_DATA_CONFIG.columns,
                COLUMN_DATA_CONFIG.columnGroup
            );
            setTableData(result);
            setTotalRecords(total);
            setTotalPages(pages);
        })
        setLoading(false);
    }

    // TODO: call api to get data
    useEffect(() => {
        fetchData();
    }, [currentPage, selectedFilterValues]);

    return (
        <Fragment>
            <PickedResDatatable
                rowKey='id'
                loading={loading}
                tableHeader={visibleColumns}
                tableData={tableData}
                selectedRows={selectedRows}
                setSelectedRows={setSelectedRows}
                totalRecords={totalRecords}
                recordsPerPage={20}
                totalPages={totalPages}
                currentPage={currentPage}
                setCurrentPage={setCurrentPage}
                visibleColumns={visibleColumns}
                setVisibleColumns={setVisibleColumns}
                sidebarVisibility={sidebarVisibility}
                setSidebarVisibility={setSidebarVisibility}
                selectedFilterValues={selectedFilterValues}
                setSelectedFilterValues={setSelectedFilterValues}
            />
        </Fragment>
    );
};

export default RawResTable;
